/****************************************************************************************/
/***************************** Page Setup */
/****************************************************************************************/
html,
body {
    height: 100%;
}

body {
    color: $gray-900;
    font-family: $font-family-base;
}

:root {
    font-size: 62.5%;
}

#page-wrapper,
.wrapper {
    padding: 0px;
    z-index: 50;
    position: relative;
}

.footer-wrapper {
    background-color: $primary;
}

/* blog index */
.container-posts article {
    margin-bottom: 35px;
}

.container-posts .post .entry-content p:last-child {
    margin-bottom: 5px;
}

@media only screen and (min-width: 1px) {

    .blog .entry-header,
    .blog .entry-content,
    .single .entry-content,
    .single .entry-header {
        padding: 0px 15px;
    }
}

@media only screen and (min-width: 576px) {

    .container-narrow,
    .page-template-default .page .entry-content,
    .page-container .entry-header,
    .full-width-page-wrapper>.container {
        width: 100%;
        max-width: 100%;
        margin: 0px auto;
    }

    .entry-content {
        overflow: visible;
    }

}

@media only screen and (min-width: 768px) {

    .container-narrow,
    .page-container .entry-header,
    .page-template-default .page .entry-content .container {
        width: 100%;
    }

}

@media only screen and (min-width: 992px) {

    .container-narrow,
    .page-template-default .page .entry-content,
    .page-container .entry-header,
    #full-width-page-wrapper>.container {
        width: 100%;
    }

    .container-narrow,
    .single .entry-header {
        max-width: 960px;
    }

    .single .entry-header,
    .single .entry-image {
        margin: 0px auto;
    }

    .blog .entry-header,
    .blog .entry-content,
    .single .entry-content,
    .single .entry-header {
        padding: 0px;
    }


}

@media only screen and (min-width: 1200px) {

    .container-narrow {
        width: 960px;
        margin: 0px auto;
    }

    .page-template-default .page .entry-content,
    .page-container .entry-header,
    #full-width-page-wrapper>.container {
        width: 1140px;
        margin: 0px auto;
    }

}

/****************************************************************************************/
/* Typography */
/****************************************************************************************/

.entry-content,
.editor-styles-wrapper p,
.editor-styles-wrapper .container p,
.editor-styles-wrapper .editor-rich-text .mce-content-body p,
.editor-styles-wrapper .wp-block-paragraph,
.editor-styles-wrapper .wp-block-quote p,
.editor-styles-wrapper .wp-block-file,
.wp-block-pullquote,
.wp-block-pullquote blockquote p,
.editor-default-block-appender textarea.editor-default-block-appender__content,
#wrapper-footer {
    font-size: 2rem;
    word-wrap: break-word;
    font-family: $font-family-base;
}

.entry-content p,
.editor-styles-wrapper .wp-block-paragraph,
.editor-styles-wrapper .editor-rich-text .mce-content-body p,
.editor-styles-wrapper .editor-rich-text blockquote p,
.editor-styles-wrapper .wp-block-quote p,
.wp-block-pullquote.is-style-solid-color blockquote p,
.editor-styles-wrapper p.mce-content-body,
.editor-styles-wrapper .editor-rich-text p.mce-content-body,
.wp-block-pullquote blockquote>.block-library-pullquote__content .editor-rich-text__tinymce[data-is-empty=true]::before,
.wp-block-pullquote blockquote>.editor-rich-text p {
    margin-bottom: 30px;
    line-height: 2;
}

.editor-styles-wrapper .editor-rich-text .wp-block-quote__citation,
.entry-content .wp-block-quote cite,
.wp-block-pullquote cite,
.wp-block-pullquote footer,
.wp-block-pullquote__citation,
.wp-block-pullquote.is-style-solid-color blockquote .editor-rich-text .wp-block-pullquote__citation {
    font-size: 1.3rem;
    line-height: 1;
    color: $gray-500;
    font-style: normal;
}

.entry-content h1,
.entry-content .h1,
.wp-block-heading h1,
.entry-header h1 {
    font-size: 4rem
}

.entry-content h2,
.entry-content .h2,
.wp-block-heading h2 {
    font-size: 3.6rem
}

.entry-content h3,
.entry-content .h3,
.wp-block-heading h3 {
    font-size: 3rem
}

.entry-content h4,
.entry-content .h4,
.wp-block-heading h4 {
    font-size: 2.8rem
}

.entry-content h5,
.entry-content .h5,
.wp-block-heading h5 {
    font-size: 2rem
}

.entry-content h6,
.entry-content .h6,
.wp-block-heading h6 {
    font-size: 1.4rem
}

.entry-header h2 {
    font-size: 3rem;
}

.entry-meta,
.entry-meta a {
    text-transform: uppercase;
    letter-spacing: .05rem;
    line-height: 1;
    font-size: 1.4rem;
    margin-bottom: 1rem;
    color: $gray-500;
}

.has-small-font-size,
.has-regular-font-size,
.has-normal-font-size,
.has-medium-font-size,
.has-large-font-size,
.has-larger-font-size,
.has-huge-font-size {
    line-height: 1.4;
}


.has-small-font-size {
    font-size: 1rem !important;
}

.has-regular-font-size,
.has-normal-font-size {
    font-size: 2rem !important
}

.has-medium-font-size {
    font-size: 3rem !important;
}

.has-large-font-size {
    font-size: 4rem !important;
}

.has-larger-font-size,
.has-huge-font-size {
    font-size: 7rem !important;
}

.entry-content p[class*="font-size"] {
    line-height: 1.2;
}

p.wp-block-subhead {
    font-family: $font-family-sans-serif;
    font-weight: 300;
}

/* links in content */
a {
    transition: .3s color;
}

a:hover {
    text-decoration: none;
}

/* Responsive Typography */
@media only screen and (min-width: 1px) and (max-width: 575px) {

    .has-large-font-size {
        font-size: 3rem !important;
    }

    .has-huge-font-size {
        font-size: 4rem !important;
    }

    .has-larger-font-size,
    .has-huge-font-size {
        line-height: 1 !important;
    }

    .single .entry-header h1 {
        line-height: 1.2;
        font-size: 3rem;
    }

    .entry-content p {
        line-height: 1.8;
        font-size: 1.6rem;
    }

}


/****************************************************************************************/
/* WordPress Core Alignment Classes + Containers */
/****************************************************************************************/

@media only screen and (min-width: 1px) {

    .entry-content>.alignfull,
    .entry-content .container [class="wp-block-*"][data-align="full"],
    .entry-content .container .alignfull,
    .wp-block[data-align="full"] {
        width: 100vw;
        max-width: 100vw;
        margin-left: calc((-100vw + 100%) / 2);
        margin-right: calc((-100vw + 100%) / 2);
    }

    .page-left-sidebar .entry-content .container .alignfull,
    .page-left-sidebar .entry-content .container [class="wp-block-*"][data-align="full"],
    .page-left-sidebar .alignfull,
    .page-left-sidebar .wp-block[data-align="full"],
    .page-right-sidebar .entry-content .container .alignfull,
    .page-right-sidebar .entry-content .container [class="wp-block-*"][data-align="full"],
    .page-right-sidebar .alignfull,
    .page-right-sidebar .wp-block[data-align="full"] {
        margin-left: calc((-100vw + 100%) / 2);
        margin-right: calc((-100vw + 100%) / 2);
    }

    .page-template-default .entry-content>.wp-block-columns {
        padding-left: 15px;
        padding-right: 15px;
    }

}

@media only screen and (min-width: 576px) {

    .entry-content>.alignfull,
    .entry-content .container [class="wp-block-*"][data-align="full"],
    .entry-content .container .alignfull,
    .wp-block[data-align="full"] {
        width: 100vw;
        max-width: 100vw;
        margin-left: calc((-100vw + 100%) / 2);
        margin-right: calc((-100vw + 100%) / 2);
    }

    .page-left-sidebar .entry-content .container .alignfull,
    .page-left-sidebar .entry-content .container [class="wp-block-*"][data-align="full"],
    .page-left-sidebar .alignfull,
    .page-left-sidebar .wp-block[data-align="full"] {
        margin-left: calc((-100vw + 80%) / 2);
        margin-right: calc((-100vw + 100%) / 2);
    }

    .page-right-sidebar .entry-content .container .alignfull,
    .page-right-sidebar .entry-content .container [class="wp-block-*"][data-align="full"],
    .page-right-sidebar .alignfull,
    .page-right-sidebar .wp-block[data-align="full"] {
        margin-left: calc((-100vw + 120%) / 2);
        margin-right: calc((-100vw + 100%) / 2);
    }

}

@media only screen and (min-width: 768px) {

    .page-left-sidebar .entry-content .container .alignfull,
    .page-left-sidebar .entry-content .container [class="wp-block-*"][data-align="full"],
    .page-left-sidebar .alignfull,
    .page-left-sidebar .wp-block[data-align="full"] {
        margin-left: calc((-100vw + 80%) / 2);
        margin-right: calc((-100vw + 100%) / 2);
    }

    .page-right-sidebar .entry-content .container .alignfull,
    .page-right-sidebar .entry-content .container [class="wp-block-*"][data-align="full"],
    .page-right-sidebar .alignfull,
    .page-right-sidebar .wp-block[data-align="full"] {
        margin-left: calc((-100vw + 120%) / 2);
    }
}

@media only screen and (min-width: 992px) {

    .entry-content .container .wp-block-pullquote,
    .entry-content .container .wp-block-quote,
    .entry-content .container .wp-block-gallery,
    .entry-content .container .wp-block-embed {
        width: 100%;
        max-width: 960px;
        margin-left: auto;
        margin-right: auto;
    }

    .page-template-default .entry-content .alignwide,
    .page-template-default .entry-content>p,
    .page-template-default .entry-content>h1,
    .page-template-default .entry-content>h2,
    .page-template-default .entry-content>h3,
    .page-template-default .entry-content>h4,
    .page-template-default .entry-content>h5,
    .page-template-default .entry-content>h6,
    .page-template-default .entry-content>.wp-block-columns,
    .page-template-default .entry-content>.wp-block-button {
        width: 100%;
        max-width: 100vw;
    }

    .page-template-default .entry-content>.alignfull,
    .entry-content .container .wp-block-embed.alignfull,
    .page-template-default .entry-content>.wp-block-columns.alignfull {
        width: 100vw;
        max-width: 100vw;
        margin-left: calc((-100vw + 100%) / 2);
        margin-right: calc((-100vw + 100%) / 2);
    }
}

@media only screen and (min-width: 1200px) {

    .page-template-default .entry-content .alignwide,
    .page-template-default .entry-content>p,
    .page-template-default .entry-content>h1,
    .page-template-default .entry-content>h2,
    .page-template-default .entry-content>h3,
    .page-template-default .entry-content>h4,
    .page-template-default .entry-content>h5,
    .page-template-default .entry-content>h6,
    .page-template-default .entry-content>.wp-block-columns,
    .page-template-default .entry-content>.wp-block-button {
        width: 1140px;
        max-width: 100%;
    }

    .page-template-default .entry-content .alignfull,
    .page-template-default .page .entry-content .alignfull,
    .page-template-default .entry-content>.wp-block-columns.alignfull {
        width: 100vw !important;
        max-width: 100vw;
        margin-left: calc((-100vw + 100%) / 2);
        margin-right: calc((-100vw + 100%) / 2);
    }

    .page-template-default .entry-content hr:after {
        display: block;
        content: '';
        clear: both;
        padding-bottom: 60px;
    }

}

.alignleft {
    display: inline;
    float: left;
    margin-right: 1.5em;
}

.alignright {
    display: inline;
    float: right;
    margin-left: 1.5em;
}

.aligncenter {
    clear: both;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1em;
}

.wp-block-image .alignleft {
    margin-left: 0px;
    margin-right: 30px;
}

.wp-block-image .alignright {
    margin-right: 0px;
    margin-left: 30px;
}

.wp-caption {
    margin-bottom: 1.5em;
    max-width: 100%;
}

.wp-caption img[class*="wp-image-"] {
    display: block;
    margin: 0 auto;
}

.wp-caption-text {
    text-align: center;
    font-size: 1.1rem;
    line-height: 1.3rem;
    margin-top: 0;
    text-transform: uppercase;
    font-weight: $font-weight-bold;
}

.wp-caption .wp-caption-text {
    margin: .8075em 0;
}

/****************************************************************************************/
/* WordPress Navigation*/
/****************************************************************************************/
.header-navbar {
    z-index: 99;
    position: relative;
}

.navbar {
    padding: 0px;
}

.navbar-primary {
    position: relative;
}

.navbar .navbar-nav {
    min-width: 80%;
}

.navbar-small {
    max-height: 55px;
    transition: opacity .4s ease-in-out;
    background-color: $light;
}

.navbar-small .navbar-brand .c9-custom-logo {
    max-height: 50px;
    width: auto;
}

.navbar-toggler {
    margin: 0px;
    padding: 5px;
    font-size: 2rem;
    line-height: 2;
    border: none;
}

.navbar-toggler:focus {
    outline: none;
    color: $primary;
}

.nav-search,
.nav-toggle {
    display: inline-block;
    font-size: 2rem;
    line-height: 2;
}

.nav-search {
    margin-right: 10px;
}

.navbar .navbar-nav .nav-item .nav-link,
.navbar .navbar-nav .nav-item .dropdown-menu .nav-item .dropdown-item {
    text-transform: uppercase;
    font-size: 2rem;
    line-height: 1;
    letter-spacing: .09rem;
    font-family: $headings-font-family;
    font-weight: $font-weight-bold;
    transition: color .3s ease-in;
}

/* nav colors by default on light theme */
.navbar-light .navbar-nav .nav-link {
    color: $gray-500;
}

.navbar-light .navbar-toggler,
.navbar-light .nav-search .btn-nav-search,
.navbar-light .navbar-nav .dropdown-item {
    color: $link-color;
}

/* page default has light text  + icon links cause of the large image behind it */
.page-template-default .navbar-light .navbar-nav .nav-link,
.page-template-default .navbar-light .navbar-toggler,
.page-template-default .navbar-light .nav-search .btn-nav-search {
    color: $light;
}

/* nav text links + icons after scrolling with white background fading in */
.navbar-small .navbar-nav .nav-item .nav-link,
.navbar-small .navbar-nav .nav-item .dropdown-menu .nav-item .dropdown-item,
.navbar-small.navbar-light .navbar-toggler,
.navbar-small.navbar-light .nav-search .btn-nav-search {
    color: $gray-900;
}

/* hover text links */
.navbar .navbar-nav .nav-item .nav-link:hover,
.navbar .navbar-nav .nav-item .dropdown-menu .nav-item .dropdown-item:hover {
    color: $link-hover-color;
}

@media only screen and (min-width: 1px) {

    .navbar-brand .c9-custom-logo {
        width: auto;
        max-height: 50px;
        height: auto;
    }

    #page .navbar-small-mobile {
        padding-bottom: 0px;
    }

    .navbar {
        display: block;
    }

    .navbar .container .navbar-small-buttons {
        justify-content: flex-end;
    }

    .btn-nav-search,
    .navbar-toggler {
        margin: 0px;
        padding: 5px;
    }

    .header-navbar.jumpfix {
        height: 55px;
    }

}

@media only screen and (min-width: 768px) {

    .navbar-brand .c9-custom-logo {
        max-height: 95px;
        height: auto;
    }

    .header-navbar {
        max-height: 100px;
        height: 100px;
    }

    .header-navbar.jumpfix {
        height: 100px;
    }

}

@media only screen and (min-width: 1px) and (max-width: 991px) {

    .navbar-expand-lg>.container,
    .navbar-expand-lg>.container-fluid {
        padding-left: 15px;
        padding-right: 15px;
    }

    .navbar-small {
        background-color: $light;
    }

    .navbar-primary {
        position: absolute;
        top: 0px;
        right: -100%;
        height: 100vh;
        width: 50vw;
        background-color: $light;
        text-align: center;
    }

    .navbar-primary.show {
        right: 0%;
    }

    /* page default needs dark text since the nav is collapsed */
    .page-template-default .navbar-light .navbar-nav .nav-link {
        color: $primary;
    }

    .navbar-primary .navbar-nav {
        width: 100%;
        display: block;
    }

    .navbar-primary .navbar-nav .nav-item {
        display: block;
        padding: 15px 0px;
    }

    .navbar-primary .navbar-nav .nav-item .nav-link {
        font-size: 2rem;
    }

    .navbar-primary .navbar-nav .nav-item .dropdown-menu {
        width: 100%;
        margin: 0px auto;
        text-align: center;
    }

}

@media only screen and (min-width: 992px) {

    .nav-search {
        display: none;
    }

}



/****************************************************************************************/
/* WordPress Core Widgets */
/****************************************************************************************/
.widget_archive ul,
.widget_categories ul,
.widget_pages ul,
.widget_meta ul,
.widget_recent_comments ul,
.widget_recent_entries ul,
.widget_rss ul,
.widget_text ul,
.widget_nav_menu ul,
.widget_cortex_twitter_widget ul,
.widget ul {
    list-style: none;
    margin: 15px 0;
    padding: 0;
}

.widget_archive ul li,
.widget_categories ul li,
.widget_categories ul li,
.widget_pages ul li,
.widget_meta ul li,
.widget_recent_comments ul li,
.widget_recent_entries ul li,
.widget_rss ul li,
.widget_text ul li,
.widget_nav_menu ul li,
.widget ul li {
    font-size: 1.4rem;
    margin: 10px 0;
    padding-left: 0px;
}

.widget {
    margin: 0 0 5em;
    font-size: 1.4rem;
}

.widget:last-child {
    margin: 0;
}

.widget h1,
.widget h2,
.widget h3 {
    font-weight: $font-weight-bold;
    letter-spacing: .08rem;
    font-size: 2rem;
}

.widget select {
    max-width: 100%;
}

.widget_nav_menu ul li ul {
    margin: 8px 0 8px 20px;
}

.widget_nav_menu ul li ul li ul {
    margin: 8px 0 8px 40px;
}

/****************************************************************************************/
/* WordPress Comment, Page, & Post Navigation */
/****************************************************************************************/

.site-main .comment-navigation,
.site-main .posts-navigation,
.site-main .post-navigation {
    margin: 3rem 0;
    overflow: hidden;
    font-size: 1.4rem;
    line-height: 1.2;
}

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
    float: left;
    width: 50%;
}

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
    float: right;
    text-align: right;
    width: 50%;
}

/****************************************************************************************/
/* WordPress Comments */
/****************************************************************************************/

.comments-area {
    max-width: 960px;
    margin: 0px auto;
    padding: 15px;
}

.comment-list {
    margin-bottom: 70px;
}

.comment-list .comment-meta {
    line-height: 1.6;
}

.comment-list .comment article {
    position: relative;
    border: none;
    border-left: 1px solid $gray-100;
}

.comment-list {
    list-style: none;
    margin: 0 0 70px;
    padding: 0;
}

.comment-list .comment article:only-child,
.comment-list .comment .children {
    margin-bottom: 25px;
    margin-top: 25px;
}

.comment-list .comment .children {
    padding-left: 20px;
}

.comment-list .comment article .says {
    display: none;
}

.comment-list .comment article .avatar {
    float: left;
}

.comment-reply-link {
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 4px 6px 4px 8px;
    color: $light;
    line-height: 1;
    z-index: 1;
    font-weight: $font-weight-base;
    letter-spacing: .2rem;
    text-transform: uppercase;
}

.comment-list .comment article .reply a:after {
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    height: 100%;
    width: 100%;
    opacity: 1;
    z-index: -1;
    transition: all .4s ease-in-out;
    background-color: $primary;
}

.comment-list .comment article .reply a:hover:after {
    opacity: .8;
}

#comments h3:after {
    border-bottom: 2px solid $primary;
}

.children {
    list-style: none;
}

.comment-content a {
    word-wrap: break-word;
}

.comment-content .wp-smiley {
    border: none;
    margin-bottom: 0;
    margin-top: 0;
    padding: 0;
}

.comment-meta a {
    font-size: 1.2rem;
}

.comment-awaiting-moderation {
    display: inline;
    font-size: 1rem;
}

.comment-author .fn,
.comment-metadata a,
.comment-awaiting-moderation {
    margin-left: 15px;
}

.comment-content:before,
.comment-content:after {
    content: "";
    display: table;
}

.comment-content:after {
    clear: both;
}

.comment-reply-link,
.comment-author .fn .url,
.comment-metadata a {
    transition: all .4s ease-in-out;
}

.comment-metadata a {
    font-weight: $font-weight-base;
    text-transform: uppercase;
}

.comment.bypostauthor {
    border-left: 3px solid $primary;
}

.comment-reply-link:hover,
.comment-author .fn .url:hover,
.comment-metadata a:hover {
    text-decoration: none;
}

.bypostauthor {
    display: block;
}

.comment-metadata a {
    color: $primary;
}

.comment-respond .form-submit {
    margin-bottom: 0px;
}

@media only screen and (min-width: 1px) {

    .comment-metadata a,
    .comment-reply-link {
        font-size: 1.1rem;
        line-height: 1;
        letter-spacing: .1rem;
    }

    .comment-author .fn .url,
    .comment-author .fn {
        font-size: 1.6rem;
    }

    .comment-content p {
        margin-left: 95px;
        margin-top: 0px;
        font-size: 1.4rem;
        line-height: 1.4;
    }

    .comment-list .comment article {
        padding-bottom: 20px;
    }

}

@media only screen and (min-width: 576px) {

    .comment-author .fn,
    .comment-author .fn .url {
        font-weight: $font-weight-bold;
        letter-spacing: .05rem;
        font-size: 1.4rem;
    }
}

@media only screen and (min-width: 768px) {

    .comment-content p {
        margin-top: 10px;
    }

}

/****************************************************************************************/
/* Sidebars */
/****************************************************************************************/
@media only screen and (min-width: 1px) {

    #left-sidebar,
    #right-sidebar {
        display: none;
    }
}

