/****************************************************************************************/
/* WordPress Gutenberg Pull Quotes and Block Quotes */
/****************************************************************************************/
.editor-styles-wrapper blockquote:before,
.editor-styles-wrapper blockquote:after,
.editor-styles-wrapper q:before,
.editor-styles-wrapper q:after,
.entry-content blockquote:before,
.entry-content blockquote:after,
.entry-content q:before,
.entry-content q:after {
    content: "";
}

/* editor block quote style */
.editor-styles-wrapper blockquote,
.entry-content blockquote,
.wp-block-quote:not(.is-large):not(.is-style-large) {
    quotes: """";
    position: relative;
    margin: 20px auto 30px auto;
    padding: 0 40px;
    border-left: 3px solid $primary;
}

.editor-styles-wrapper blockquote:before,
.entry-content blockquote:before {
    content: "\201C";
    letter-spacing: .2rem;
    text-transform: uppercase;
    position: absolute;
    top: -40px;
    left: -50px;
    font-size: 7rem;
    font-weight: $font-weight-normal;
    font-family: $headings-font-family;
    color: $primary;
}

/* editor pull quote has lines style */
.wp-block-pullquote {
    border-top: 4px solid $primary;
    border-bottom: 4px solid $primary;
    color: $gray-900;
    padding: 30px 0px;
}

/* editor pull quote has regular style */
.wp-block-pullquote.is-style-solid-color,
.editor-styles-wrapper .wp-block-pullquote.is-style-solid-color {
