        line-height: 2;
    }

}


/****************************************************************************************/
/* WordPress Gutenberg ol ul li (Ordered and unordered lists) */
/****************************************************************************************/
.entry-content ul:not(.wp-block-gallery),
.entry-content ol,
.editor-styles-wrapper .editor-rich-text ol,
.editor-styles-wrapper .editor-rich-text ul:not(.wp-block-gallery) {
